/*################################*\
    c-Permalink.less
\*################################*/
.c-Permalink {
  margin-left: 1rem;
  font-weight: 100;
}
@supports (margin-inline-start: 1rem) {
  .c-Permalink {
    margin-left: unset;
    margin-inline-start: 1rem;
  }
}
h1:hover > .c-Permalink,
h2:hover > .c-Permalink,
h3:hover > .c-Permalink,
h4:hover > .c-Permalink,
h5:hover > .c-Permalink,
h6:hover > .c-Permalink {
  display: inline;
  position: static;
  position: initial;
  position: unset;
}
